import View from './view.js'; //
//
import icons from 'url:../../img/icons.svg'; // using parcel 2 and if is video
import previewView from './previewView.js';
class cartsView extends View {
  _parentElement = document.querySelector('.cart__list');
  _errorMessage = 'No List yet. find a nice recipe to List!';
  _message = '';

  addHandlerRender(handler) {
    window.addEventListener('load', handler);
  }
  _generateMarkup() {
    //looping thru the array
    return this._data.map(cart => previewView.render(cart, false)).join('');
  }
}

export default new cartsView();
